/* Footer Styles */
.footer-container {
  background-color: #282c34;
  color: #ffffff;
  text-align: center;
  padding: 20px 10px;
  font-size: 14px;
  border-top: 2px solid #ff4081;
}

.footer-container p {
  margin: 0;
  font-family: 'Arial', sans-serif;
}

.footer-container a {
  color: #ff4081;
  text-decoration: none;
  font-weight: bold;
}

.footer-container a:hover {
  text-decoration: underline;
}
